/*
  Markdown
*/
/*! Markdown.css | MIT License | github.com/picturepan2/markdown.css */
.markdown-css {
  font-size: 1.6rem;
  line-height: 1.75;
}

.markdown-css h1,
.markdown-css h2,
.markdown-css h3,
.markdown-css h4,
.markdown-css h5,
.markdown-css h6 {
  color: #3b3b3b;
  font-weight: 700;
  line-height: 1.5;
  margin-bottom: 24px;
  margin-top: 8px;
}

.markdown-css h1+hr,
.markdown-css h2+hr,
.markdown-css h3+hr,
.markdown-css h4+hr,
.markdown-css h5+hr,
.markdown-css h6+hr {
  margin-top: -8px;
}

.markdown-css h1,
.markdown-css h2 {
  font-size: 2.4rem;
  // margin: 4.8rem 0 2.4rem;
  font-weight: 700;
  background: $primary-color;
  color: $white;
  border-radius: 0.4rem;
  padding: 0.8rem 1.2rem;
}

.markdown-css h3 {
  font-size: 2.2rem;
  margin: 2.4rem 0 1.6rem;
  border-left: 0.4rem solid $primary-color;
  padding-left: 1.6rem;
}

.markdown-css h4 {
  font-size: 2rem;
  margin: 2rem 0 1.2rem;
}

.markdown-css h5 {
  font-size: 1.8rem;
}

.markdown-css h6 {
  font-size: 1.6rem;
}

.markdown-css p {
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
  margin: 0 0 32px;
}

.markdown-css a {
  color: #0056ff;
  text-decoration: none;
}

.markdown-css a:focus,
.markdown-css a:hover,
.markdown-css a:active {
  text-decoration: underline;
}

.markdown-css a,
.markdown-css ins,
.markdown-css u {
  -webkit-text-decoration-skip: ink edges;
  text-decoration-skip: ink edges;
}

.markdown-css del+del,
.markdown-css del+s,
.markdown-css ins+ins,
.markdown-css ins+u,
.markdown-css s+del,
.markdown-css s+s,
.markdown-css u+ins,
.markdown-css u+u {
  margin-left: .125em;
}

.markdown-css hr {
  background: #ededed;
  border: none;
  display: block;
  height: 1px;
  margin: 8px 0 24px;
  overflow: hidden;
}

.markdown-css hr::after {
  clear: both;
  content: "";
  display: table;
}

.markdown-css ul,
.markdown-css ol {
  margin: 32px 0 32px 32px;
  padding: 0;
}

.markdown-css ul ul,
.markdown-css ol ul,
.markdown-css ul ol,
.markdown-css ol ol {
  margin: 16px 0 16px 32px;
}

.markdown-css ul li,
.markdown-css ol li {
  margin-top: 8px;
}

.markdown-css ul {
  list-style: disc inside;
}

.markdown-css ul ul {
  list-style-type: circle;
}

.markdown-css ol {
  list-style: decimal inside;
}

.markdown-css ol ol {
  list-style-type: lower-alpha;
}

.markdown-css img {
  border: 0;
  height: auto;
  max-width: 100%;
}

.markdown-css img+em {
  color: #a1a1a1;
  display: inline-block;
  font-size: 14px;
  text-align: center;
  width: 100%;
}

.markdown-css blockquote {
  background: #f0f0f0;
  margin: 32px 0;
  padding: 24px;
}

.markdown-css blockquote::before {
  color: #ededed;
  content: open-quote;
  font-family: arial;
  font-size: 6em;
  line-height: .1em;
  vertical-align: -.5em;
}

.markdown-css blockquote p:last-child {
  margin-bottom: 0;
}

.markdown-css table {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 32px 0;
  text-align: left;
  width: 100%;
}

.markdown-css table tbody tr:nth-of-type(odd) {
  background: #f0f0f0;
}

.markdown-css table td,
.markdown-css table th {
  border-bottom: 1px solid #ededed;
  padding: 8px 4px;
}

.markdown-css table th {
  border-bottom-width: 2px;
}

.markdown-css code {
  background: #f0f0f0;
  font-family: "SF Mono", "Segoe UI Mono", "Roboto Mono", Menlo, Courier, monospace;
  font-size: .9em;
  padding: .05em;
}

.markdown-css pre {
  background: #f0f0f0;
  margin: 32px 0;
  overflow-x: auto;
}

.markdown-css pre code {
  color: inherit;
  display: block;
  line-height: inherit;
  padding: 16px;
}
